.experience {
  background: linear-gradient(135deg, rgba(26, 26, 46, 0.8) 0%, rgba(22, 33, 62, 0.8) 100%);
}

/* Timeline */
.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto 80px;
  padding: 40px 0;
}

.timeline::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 4px;
  height: 100%;
  background: linear-gradient(45deg, #64ffda, #00d4ff);
  transform: translateX(-50%);
  border-radius: 2px;
}

.timeline-item {
  position: relative;
  margin-bottom: 60px;
  width: 100%;
}

.timeline-item.left {
  padding-right: 50%;
}

.timeline-item.right {
  padding-left: 50%;
}

/* Tech Stack Section */
.tech-stack-section {
  position: absolute;
  top: 10%;
  transform: translateY(-50%);
  width: 220px;
  z-index: 10;
}

/* Mobile Tech Stack - Inside Cards */
.mobile-tech-stack {
  display: none;
  margin-bottom: 20px;
}

.mobile-tech-stack h4 {
  font-size: 1rem;
  margin-bottom: 12px;
  color: #64ffda;
  font-weight: 600;
}

.mobile-tech-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.mobile-tech-tag {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  background: rgba(100, 255, 218, 0.1);
  border: 1px solid rgba(100, 255, 218, 0.3);
  border-radius: 15px;
  color: #64ffda;
  font-size: 0.75rem;
  font-weight: 500;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.mobile-tech-tag svg {
  font-size: 0.8rem;
  flex-shrink: 0;
}

.mobile-tech-tag:hover {
  background: rgba(100, 255, 218, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(100, 255, 218, 0.2);
}

.timeline-item.left .tech-stack-section {
  right: 150px;
}

.timeline-item.right .tech-stack-section {
  left: 150px;
}

.tech-stack-section h4 {
  display: none;
}

.tech-stack-grid {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.tech-card {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  font-size: 14px;
  letter-spacing: 0.5px;
  min-width: 200px;
}

.tech-card:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.tech-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  font-size: 18px;
  color: #64ffda;
}

.tech-name {
  color: #ffffff;
  font-weight: 500;
  white-space: nowrap;
}

.timeline-content {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 30px;
  position: relative;
  transition: all 0.3s ease;
  overflow: hidden;
  min-height: 200px;
}

.timeline-content::before {
  content: '';
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  border: 15px solid transparent;
}

.timeline-item.left .timeline-content::before {
  right: -30px;
  border-left-color: rgba(255, 255, 255, 0.05);
  transform: translateY(-50%);
}

.timeline-item.right .timeline-content::before {
  left: -30px;
  border-right-color: rgba(255, 255, 255, 0.05);
  transform: translateY(-50%);
}

.timeline-content:hover {
  transform: translateY(-5px);
  border-color: rgba(100, 255, 218, 0.3);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.timeline-header {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 20px;
}

.timeline-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(45deg, #64ffda, #00d4ff);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #0f0f23;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.timeline-info h3 {
  font-size: 1.4rem;
  margin-bottom: 8px;
  color: #ffffff;
  font-weight: 600;
}

.company-info {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.company {
  font-size: 1.1rem;
  color: #64ffda;
  font-weight: 500;
}

.meta {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.period,
.location {
  display: flex;
  align-items: center;
  gap: 5px;
  color: #8892b0;
  font-size: 0.9rem;
}

.period svg,
.location svg {
  font-size: 0.8rem;
}

/* Technologies */
.technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.tech-tag {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  background: rgba(100, 255, 218, 0.1);
  border: 1px solid rgba(100, 255, 218, 0.3);
  border-radius: 20px;
  color: #64ffda;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
  cursor: default;
  backdrop-filter: blur(10px);
}

.tech-tag svg {
  font-size: 1rem;
  flex-shrink: 0;
}

.tech-tag span {
  white-space: nowrap;
}

.tech-tag:hover {
  background: rgba(100, 255, 218, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(100, 255, 218, 0.2);
  border-color: rgba(100, 255, 218, 0.5);
}

/* Achievements */
.achievements h4 {
  font-size: 1.1rem;
  margin-bottom: 15px;
  color: #64ffda;
  font-weight: 600;
}

.achievements ul {
  list-style: none;
  padding: 0;
}

.achievements li {
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  color: #8892b0;
  line-height: 1.6;
}

.achievements li::before {
  content: '▸';
  position: absolute;
  left: 0;
  color: #64ffda;
  font-weight: bold;
  font-size: 1.2rem;
}

/* Timeline Connector */
.timeline-connector {
  position: absolute;
  top: 50%;
  width: 20px;
  height: 20px;
  background: linear-gradient(45deg, #64ffda, #00d4ff);
  border-radius: 50%;
  border: 4px solid #0f0f23;
  transform: translateY(-50%);
}

.timeline-item.left .timeline-connector {
  right: -10px;
}

.timeline-item.right .timeline-connector {
  left: -10px;
}

/* Experience Summary */
.experience-summary {
  text-align: center;
}

.summary-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 40px;
  max-width: 800px;
  margin: 0 auto;
}

.summary-card h3 {
  font-size: 2rem;
  margin-bottom: 30px;
  color: #64ffda;
  font-weight: 600;
}

.summary-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 30px;
}

.stat {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 2.5rem;
  font-weight: 700;
  color: #64ffda;
  margin-bottom: 8px;
  background: linear-gradient(45deg, #64ffda, #00d4ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-label {
  font-size: 0.9rem;
  color: #8892b0;
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 968px) {
  .timeline::before {
    display: none;
  }

  .timeline {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .timeline-item.left,
  .timeline-item.right {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    max-width: 600px;
    margin-bottom: 30px;
  }

  .timeline-content {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
  }

  .timeline-item.left .timeline-content::before,
  .timeline-item.right .timeline-content::before {
    display: none;
  }

  .timeline-item.left .timeline-connector,
  .timeline-item.right .timeline-connector {
    display: none;
  }

  /* Hide desktop tech stack on mobile */
  .desktop-only {
    display: none !important;
  }

  /* Show mobile tech stack */
  .mobile-tech-stack {
    display: block;
  }
}

@media (max-width: 768px) {
  .timeline {
    padding: 20px 0;
  }
  
  .timeline-content {
    padding: 20px;
  }
  
  .timeline-header {
    flex-direction: column;
    text-align: center;
    gap: 15px;
    align-items: center;
    width: 100%;
  }
  
  .timeline-header > div:last-child {
    width: 100%;
    text-align: center;
  }
  
  .timeline-info {
    width: 100%;
    text-align: center;
  }
  
  .timeline-info h3 {
    text-align: center !important;
    width: 100%;
    display: block;
  }
  
  .company-info {
    text-align: center;
    width: 100%;
  }
  
  .timeline-icon {
    margin: 0 auto;
  }
  
  .meta {
    justify-content: center;
    gap: 15px;
  }
  
  .technologies {
    justify-content: center;
  }
  
  .summary-card {
    padding: 30px 20px;
  }
  
  .summary-card h3 {
    font-size: 1.6rem;
  }
  
  .summary-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  
  .stat-number {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .timeline-item.left,
  .timeline-item.right {
    padding-left: 0;
    padding-right: 0;
    margin: 0 10px 25px 10px;
    max-width: 100%;
  }
  
  .timeline-content {
    padding: 18px;
    margin: 0;
    border-radius: 15px;
    width: 100%;
  }
  
  .timeline-info h3 {
    font-size: 1.2rem;
    line-height: 1.4;
  }
  
  .company {
    font-size: 1rem;
  }
  
  .meta {
    gap: 6px;
    margin-bottom: 12px;
  }
  
  .timeline-info h3 {
    font-size: 1.3rem;
    text-align: center;
    font-weight: 700;
    margin-bottom: 8px;
    color: #ffffff;
    line-height: 1.3;
    width: 100%;
    display: block;
  }
  
  .company {
    font-size: 1.1rem;
    text-align: center;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .meta {
    flex-direction: column;
    gap: 8px;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .summary-stats {
    grid-template-columns: 1fr;
  }
  
  .technologies {
    gap: 8px;
  }
  
  .tech-tag {
    font-size: 0.7rem;
    padding: 4px 8px;
  }
  
  /* Enhanced mobile touch support */
  .tech-card {
    min-width: 160px;
    padding: 8px 12px;
    touch-action: manipulation;
  }
  
  .achievements li {
    font-size: 0.9rem;
    line-height: 1.5;
  }
  
  /* Mobile tech stack adjustments */
  .mobile-tech-grid {
    gap: 6px;
    justify-content: center;
  }
  
  .mobile-tech-tag {
    font-size: 0.7rem;
    padding: 4px 8px;
  }
  
  .mobile-tech-tag svg {
    font-size: 0.7rem;
  }
  
  /* Center mobile tech stack heading */
  .mobile-tech-stack h4 {
    text-align: center;
  }
  
  /* Center achievements heading */
  .achievements h4 {
    text-align: center;
  }
}

@media (max-width: 360px) {
  .timeline-item.left,
  .timeline-item.right {
    padding-left: 50px;
  }
  
  .timeline-info h3 {
    font-size: 1.1rem;
  }
  
  .company {
    font-size: 0.9rem;
  }
  
  .tech-card {
    min-width: 140px;
    padding: 6px 10px;
    font-size: 0.7rem;
  }
  
  .tech-icon {
    width: 12px;
    height: 12px;
  }
  
  .tech-icon svg {
    font-size: 0.8rem;
  }
  
  .timeline-content {
    padding: 12px;
  }
  
  .achievements li {
    font-size: 0.85rem;
    padding-left: 20px;
  }
  
  .achievements li::before {
    font-size: 1rem;
  }
} 